@import url(https://fonts.googleapis.com/css?family=Open+Sans);
.sketch-picker{
    width: 275px !important;
    background: none !important;
    border: none;
    border-radius: 0px !important;
    box-shadow: none !important;
    position: absolute !important;
    -webkit-transform: translate(87%, -25px) !important;
            transform: translate(87%, -25px) !important;
    background-color: #f3f3f3 !important;
    padding: 27px 20px !important;
    display: none;
}

.sketch-picker-true{
    display: block;
}

body{
  	margin: 0;
	padding: 0;
	background-color: #e6e6e6;
	overflow-x: hidden;
}

*{
	font-family: 'Roboto', arial, sans-serif;
}

/*Modal Preset Inputs/Labels*/

.head-w{
	font-size: 24px;
	margin: 0;
}

.head-w, .label-w{
	font-weight: 200;
	color: #3D405D;
}

.label-w{
	font-size: 15px;
}

.input-w, .input-area-w{
	margin-top: 6px;
}

.input-w > input{
	width: 230px;
	height: 33px;
	background-color: #ececec;
    border: 1px solid #b2b2b200;
	outline: none;
	padding: 0 10px;
	color: #3D405D; 
	transition: .4s ease;
	font-weight: 300;
}

.input-w175 > input{
	width: 175px;
}

.input-w > input:focus{
	background-color: #fff;
	border: 1px solid #3D405D;
}

.input-w > input::-webkit-input-placeholder{
	color: #B2B2B2;
	font-size: 14px;
	font-weight: 200;
}

.input-w > input::-ms-input-placeholder{
	color: #B2B2B2;
	font-size: 14px;
	font-weight: 200;
}

.input-w > input::placeholder{
	color: #B2B2B2;
	font-size: 14px;
	font-weight: 200;
}

.input-area-w > textarea{
    width: 230px;
    height: 16px;
    background-color: #ececec;
    border: 1px solid #b2b2b200;
    outline: none;
    padding: 10px;
    color: #3D405D;
    transition: .4s ease;
    font-weight: 300;
}

.input-a175 > textarea{
	width: 175px;
}

.input-area-w > textarea:focus{
	height: 100px;
	background-color: #fff;
	border: 1px solid #3D405D;
}

.field-w{
	margin: 18px 0;
}

.range-w > input{
    width: 100%;
}

.modal-w{
	padding: 25px 25px 1px 25px;
}

.modal-g{
	z-index: 100;
}

.modal{
    z-index: 100;
}

/*Modal Bottom Buttons*/

.btn-w{
	height: 67px;
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: .35s ease;
    font-size: 14px;
    font-weight: 300;
	color: #3d405d;
	cursor: pointer;
}

.btn-w:hover{
    color: #f8f8f8;
    background-color: #3d405d;
}

.f-w{
	width: 100%;
}

.btn-container-w{
	display: flex;
    justify-content: space-around;
}
.side-modal{
    position: absolute;
    -webkit-transform: translateX(-85px);
            transform: translateX(-85px);
    z-index: 1000;
    right: 0;
    bottom: 0px;
    background-color: #FAF9FF;
}

.transition-bg{
    position: fixed;
    bottom: 0;
    width: 100vw;
    height: 0vh;
    background-color: #fff;
    transition: .4s ease;
    z-index: 999999;
}

::-webkit-scrollbar {
    width: 6px;
}

::-webkit-scrollbar-track {
    box-shadow: inset 0 0 1px grey; 
    border-radius: 10px;
}

::-webkit-scrollbar-thumb {
    background: rgb(153, 153, 153); 
    border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
    background: rgb(129, 129, 129); 
}
.navbar-container{
	display: flex;
	align-items: center;
	width: 100%;
	height: 66px; /*Responsive*/
	background-color: #3d405d;
	position: fixed;
    top: 0;
    z-index: 15;
}

.search-bar-container{
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
    position: -webkit-sticky;
    position: sticky;
    left: 50%;
}

.search-form{
	display: flex;
	align-items: center;
}

.search-input{
	transition: all .5s ease;
	height: 31px; /*Responsive*/
	width: 0vmin;
	border-radius: 50px;
	border: 3px solid #4285F4;
	outline: none;
	padding-left: 40px;
	background-image: url(/Monotone-Tab/static/media/Google.27264edf.svg);
	background-size: 21px;
    background-position: 9px;
    background-repeat: no-repeat;
}

.search-input:focus{
	width: 45vmin;
}

.search-input::-webkit-input-placeholder{
	font-style: italic;
	color: #CFCFCF;
}

.search-input::-ms-input-placeholder{
	font-style: italic;
	color: #CFCFCF;
}

.search-input::placeholder{
	font-style: italic;
	color: #CFCFCF;
}

.search-engine-icon{
	height: 100%;
}

.content-container{
	display: flex;
	justify-content: flex-end;
	align-items: center;
    width: 100%;
    height: 100%;
}

.con-date{
	display: flex;
	align-items: center;
}

.con-time, .con-login{
    color: #F8F8F8;
    font-weight: 500;
    font-size: 13px;
}

.con-login{
	margin-left: 30px;
	cursor: pointer;
}

.con-login:hover{
	text-decoration: underline;
}

.con-menu{
	height: 25%;
	margin: 0 30px;
	cursor: pointer;
}
.new-item-modal, .edit-item-modal{
	display: flex;
	justify-content: center;
	align-items: center;
    z-index: 100;
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
	background-color: #5d5d5dc2;
	transition: .1s ease;
	opacity: 0;
	pointer-events: none;
}

.modal-active{
	display: flex;
	opacity: 1;
	pointer-events: initial;
}

.new-item-modal-content{
	transition: .5s;
	background-color: #f3f3f3;
}

.color-field{
	margin: 3px 0;
}

.a-color-picker-row{
	padding: 5px 15px;
}

.a-color-picker-row-top{
	padding: 0px;
}
#QuickLinks{
	margin-top: 90px;
}

#quick-main-container{
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
    margin: 20px auto 0 auto;
}

.quick-item-container{
	position: relative;
	width: 245px;/**/
}

.quick-item{
	position: relative;
	text-decoration: none;
}

.quick-container{
	display: grid;
	margin: 10px auto;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.quick-item{
	margin: 1px;
	transition: .25s ease;
	padding-bottom: 20px;
	display: flex;
	cursor: pointer;
	flex-direction: column;
	position: relative;
}

.quick-label{
	margin-left: 6px;
    border-left: 3px solid #3D405D;
    padding-left: 5px;
    font-size: 20px;
    color: #6b6b6b;
}

.empty-item{
	background-color: #CECECE;
}

.item-let{
	width: 68px;
    height: 68px;
    margin: 20px auto 5px auto;
    text-align: center;
    background-color: #ffffff80;
    border-radius: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.empty-let{
	background-color: #74747471;
}

.let-ind{
	font-size: 30px;
    font-weight: bold;
    color: #FFF;
}

.item-name{
	font-weight: 600;
    font-size: 14px;
	color: #E6E6E6;
	text-align: center;
	margin-top: 6px;
}

.empty-name{
	color: #727272;
	text-align: center;
}

.quick-item-container:hover > .item-option-btn{
	opacity: 1;
}

.item-option-btn{
	position: absolute;
	top: 0;
	right: 0;
	width: 4px;
	transition: .4s ease;
	opacity: 0;
    padding: 5px 13px;
	margin: 13px 8px;
	border-radius: 60px;
	cursor: pointer;
}
.sidebar-container{
	position: fixed;
	overflow: hidden;
	z-index: 10;
	transition: .15s ease;
	width: 0px;
	height: 100%;
	background-color: #FAF9FF;
	display: flex;
    justify-content: center;
	align-items: flex-end;
	top: 0;
}

.sidebar-active{
	width: 85px;
}

.side-set{
	display: flex;
    flex-direction: column;
    justify-content: center;
}

.side-icon{
	cursor: pointer;
	margin: 15px auto;
	width: 22px;
}

.sidebar-items{
	-webkit-transform: translateY(-5%);
	        transform: translateY(-5%);
}
#FirstStart{
    z-index: 1100;
    position: absolute;
    top: 0;
    transition: .85s ease;
    opacity: 1;
}

#FirstStart > *{
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.welcome{
    display: flex;
    width: 100vw;
    height: 100vh;
    background-color: #0078D7;
    justify-content: center;
    align-items: center;
}

#text{
    position: absolute;
}

#text > h1, [name=signup] > h1{
    transition: 1s ease;
    opacity: 1;
    margin: 0;
    color: #fff;
    font-weight: 200;
    font-size: 40px;
    text-align: center;
    align-items: center;
}

h2{
    margin: 10px 0;
    color: #fff;
    font-weight: 200;
    font-size: 15px;
}

.signup-label{
    color: #fff;
    font-weight: 200;
    font-size: 15px;
}

p{
    margin: 0;
}

.no2{
    display: flex;
    flex-direction: column;
}

.form-signup-container{
    display: none;
    transition: 1s ease;
    opacity: 0;
}

.sign-label{
    margin: 15px 0;
}

.signup-input{
    position: relative;
    margin-top: 3px;
}

.signup-input > input{
    transition: .2s ease;
    width: -webkit-fill-available;
    border: none;
    height: 33px;
    outline: none;
    background-color: #ffffff42;
    color: #fff;
    padding: 0 10px;
}

.signup-input > input:focus{
    background-color: #fff;
    color: #000;
}

.sign-input-border{
    position: absolute;
    bottom: 0;
    width: 0%;
    height: 2px;
    background-color: #fff;
}

.signup-buttons{
    display: flex;
    justify-content: space-between;
    margin-top: 25px;
}

.sign-btn{
    cursor: pointer;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    text-align: center;
    border: 2px solid;
    padding: 12px 25px;
    font-size: 13px;
    font-weight: 500;
    border-color: #fff;
    color: #fff;
}

.sign-confirm-btn{
    background-color: #fff;
    color: #0078D7;
}
.Settings{
    position: fixed;
    width: 300px;
    overflow-y: scroll;
    overflow-x: hidden;
    bottom: 0;
    right: 85px;
    background-color: #FAF9FF;
    transition: .4s ease;
}

.Settings-Inactive{
    width: 0px;
}
.AccountModal{
    position: absolute;
    z-index: 1;
    top: 66px;
    right: 85px;
    background-color: #faf9ff;
    padding: 17px 25px;
    box-shadow: 0 2px 10px #00000033;
    transition: .1s ease;
    opacity: 1;
}

.account-modal-inactive{
    opacity: 0;
    pointer-events: none;
}

.account-field > label {
    font-size: 12px;
    color: #727272;
}

.account-password{
    margin-top: 10px;
}

.account-field > div {
    margin: 4px 0;
}

.account-field > div > input{
    outline: none;
    border: none;
    height: 27px;
    padding: 0 7px;
    color: #7b7b7b;
    box-shadow: 0 1px 3px #00000033;
}

.account-buttons{
    display: flex;
    flex-direction: row-reverse;
}

.account-btn{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 10px 19px;
    margin: 17px auto 0 auto;
    font-size: 11px;
    cursor: pointer;
}

.login-btn{
    color: #fff;
    background-color: #3d405d;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding: 11px 19px;
}

.signout-btn{
    color: #3d405d;
    border: 1px solid #3f405d;
}
.new-note-btn{
    background-color: #3D405D;
    border-radius: 50px;
    width: 60px;
    height: 60px;
    display: flex;
	justify-content: center;
    cursor: pointer;
    margin-left: 20px;
    transition: .4s ease;
    z-index: 10;
}

.new-note-btn > img{
    width: 30%;
    pointer-events: none;
}

.AddNoteModal{
    transition: .3s;
    background-color: #f3f3f3;
}

.note-modal-inactive{
    opacity: 0;
    pointer-events: none;
}

.NewNote{
    display: flex;
    align-items: flex-end;
    position: fixed;
    right: 105px;
    bottom: 20px;
}

.note-field > label{
    font-size: 12px;
    color: #727272;
}

.note-field > div {
    margin: 4px 0;
}

.note-field > div > input{
    outline: none;
    border: none;
    height: 27px;
    padding: 0 7px;
    color: #7b7b7b;
    box-shadow: 0 1px 3px #00000033;
}

.note-field > div > textarea{
    outline: none;
    border: none;
    padding: 0 7px;
    color: #7b7b7b;
    box-shadow: 0 1px 3px #00000033;
}

.note-body{
    margin-top: 9px; 
}

.note-add-btn{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 10px 19px;
    margin: 17px auto 0 auto;
    font-size: 11px;
    cursor: pointer;
    color: #fff;
    background-color: #3d405d;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding: 11px 19px;
}
.Notes{
    position: fixed;
    width: 300px;
    overflow-y: scroll;
    overflow-x: hidden;
    top: 66px;
    right: 85px;
    height: 100%;
    background-color: #FAF9FF;
    transition: .4s ease;
}

.Notes-Inactive{
    width: 0px;
}

.notes-container{
    width: 250px;
    padding: 17px 25px;
}

.notes-container > p{
    font-size: 17px;
    color: #6b6b6b;
    margin-bottom: 10px;
}

.note-cell{
    display: flex;
    position: relative;
    top: 0;
    align-items: center;
    width: 100%;
}

.note-item{
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
    border: 1px solid #d2d2d2;;
    padding: 2px 10px;
    margin: 2px 0;
}

.note-item:last-child{
    margin-bottom: 200px
}

.note-content{
    max-height: -webkit-fit-content;
    max-height: -moz-fit-content;
    max-height: fit-content;
    height: 0px;
    overflow: hidden;
}

.note-content-active{
    height: 100%;
}

.note-arrow{
    height: 14px;
    padding: 6px;
    cursor: pointer;
    transition: .2s ease;
}

.note-arrow-active{
    -webkit-transform: rotate(-270deg);
            transform: rotate(-270deg)
}

.note-title{
    margin-left: 6px;
}

.note-content > p{
    word-break: break-all;
}

.note-title > p, .note-content > p{
    font-size: 13px;
    color: #727272;
}

